<template>
<!-- Movie card standard -->
<div class='movie-card' :class="[cardStyle, carouselClass]" v-on:click="openModal">
  <div class='col1'>
    <div v-if="Poster != null" class='image-wrapper'>
      <div><img class='img-poster' :src="posterUrl"></div>
    </div>
    <div v-else class='poster-placeholder'>NO POSTER<br>AVAILABLE</div>
  </div>
  <div class='col2'>
    <div class='card-wrapper'>
      <!-- Movie Status -->
      
      <!-- Title -->
      <div class='movie-title'>{{ Title }} &nbsp;<b style='color:gray;'>({{ Year }})</b></div>
      <!-- Remarks -->
      <div class='movie-status-wrapper'>
          <div class='movie-label'>Response to request for production support:</div>
        <div class='movie-status' :class="movieStatus">{{ Status }}</div>
      </div>
      <div class='remarks-wrapper'>
          <div class='movie-label'>Remarks from the military:</div>
        <div class='movie-remarks'>"{{ Snippet }}"</div>
      </div>
    </div>
  </div>
</div>


</template>

<script>
/* import * as d3 from "d3"; */
export default {
  name: 'CarouselMovieCard',
  props: ['Title', 'Remarks', 'Snippet', 'CarouselOrder', 'Year', 'FilmReleased', 'Genre', 'ratingImdb', 'imdbVotes', 'Status', 'Poster', 'Plot'],
  data: function () {
    return {
      openStatus: false,
    }
  },
  computed: {
    carouselClass() {
        return "order" + this.CarouselOrder
    },
    movieStatus() {
      if (this.Status === "APPROVED") {
        return "approved"
      } else if (this.Status === "DENIED") {
        return "denied"
      } else if (this.Status === "LIMITED") {
        return "limited"
      } else {
        return "other"
      }
    },
    cardStyle() {
      if (this.Status === "APPROVED") {
        return "card-approved"
      } else if (this.Status === "DENIED") {
        return "card-denied"
      } else if (this.Status === "LIMITED") {
        return "card-limited"
      } else {
        return "card-other"
      }
    },
    textStyle() {
      if (this.Status === "APPROVED") {
        return "text-approved"
      } else if (this.Status === "DENIED") {
        return "text-denied"
      } else if (this.Status === "LIMITED") {
        return "text-limited"
      } else {
        return "text-other"
      }
    },
    posterUrl() {
      return this.Poster
    },

  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.movie-container {
  padding: 10px;
  margin: 10px auto;
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  background-color: var(--accent);
  color: var(--text);
  border-radius: 5px;
}

.movie-card {
  padding: 10px;
  margin: 10px;
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  background-color: var(--accent);
  color: var(--text);
  border-radius: 5px;
}

.approved {
  background-color: var(--approved);
}

.denied {
  background-color: var(--denied);
}

.limited {
  background-color: var(--limited);
}

.other {
  background-color: var(--other);
}

.text-approved {
  color: var(--approved);
}

.text-denied {
  color: var(--denied);
}

.text-limited {
  color: var(--limited);
}

.text-other {
  color: rgb(153, 151, 151);
}

.movie-title {
  font-family: var(--title-font);
  color: white;
  font-size: var(--h3-size);
  font-weight: 900;
  padding: 5px 0px;
  line-height: 23px;
}

.movie-status {
  font-size: 16px;
  width: fit-content;
  margin: 5px 0px 10px 0px;
  padding: 2px 4px 2px 4px;
  color: var(--bg-color);
  font-weight: 900;
}

.movie-label {
  font-size: 13px;
  font-weight: 600;
  margin: 15px 0px 2px 0px;
  color: gray;
}

.movie-remarks {
  font-size: 12px;
  text-align: left;
}

img {
  width: 85%;
  margin: 0px 20px 10px 0px;
  border: 0.5px solid grey;
}

@media only screen and (max-width: 900px) { 
  .movie-container {
    padding: 10px;
    margin: 5px 0px 5px 0px;
    display: grid;
    grid-template-columns: 1fr 1.5fr;
    background-color: var(--accent);
    color: var(--text);
    border-radius: 5px;
  }

  .movie-card-modal {
    padding: 10px;
    margin: 20px auto;
    display: grid;
    grid-template-columns: 1fr 1.5fr;
    background-color: var(--accent);
    color: var(--text);
    position: fixed;
    top: 5;
    bottom: 0;
    z-index: 1;
    width: 80%;
    max-width: 600px;
    height: 70%;
    overflow: auto;
  }

}


</style>
